function getHighestRank(first, second) {
  const firstRank = ranking[first] ? ranking[first].rank : 20;
  const secondRank = ranking[second] ? ranking[second].rank : 20;

  return firstRank < secondRank ? first : second;
}

export { getHighestRank };
export default ranking;
